@import "../../definitions";


.phase {
    cursor: pointer;
    
    .cont {
        margin: 0 auto;
        background-color: #fff;
        border-radius: $default-border-radius;
        width: 6rem;
        max-width: 100%;

        h6 {
            color: $primary;
            padding: 1rem 0;
            margin: 0;
        }

        .img {
            display: flex;
            align-items: center;
            justify-content: center;;
            img {
                max-width: 80%;
                max-height: 5rem;
                border-bottom: 4px solid #CABDFF;
                padding: 1rem 0;
                
            }
        }
    }
    .cont:hover {
        transform: scale(1.05);
        transition: all 0.3s;
    }
}

.phase:hover {
    text-decoration: none;
}


